.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    background: url('preLoader.svg') 70px 115px no-repeat;
    background-size: 100px 100px;
}
